import React, { Component } from "react";
import MovieSearch from "../MovieSearch/MovieSearch";
import MovieList from "../MovieList/MovieList";
import Spin from "../spin/Spin";
import "../default-style/default-style.css";

export default class MovieApp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      movies: [],
      loading: true,
    };
    this.setMovies = this.setMovies.bind(this);
  }

  setMovies(movies) {
    this.setState({ movies, loading: false }); //  loading  false   
  }

  render() {
    const { movies, loading } = this.state;
    return (
      <div className="container">
        {loading && <Spin />} {/*  Spin,  loading  true */}
        <MovieSearch
          setMovies={this.setMovies}
          setLoadingState={(isLoading) => this.setState({ loading: isLoading })}
        />{" "}
        {/*  setMovies     loading  MovieSearch */}
        <MovieList movies={movies} />
      </div>
    );
  }
}
